import Articles from './components/Articles'
import AuthDialog from '@/components/AuthDialog/AuthDialog'
import Fab from "@/components/Fab"
import Skeleton from '@/components/Skelenton'

import { parseTime } from '@/utils/time'
import { mapGetters } from 'vuex'

let option = null
export default {
  components: {
    Articles,
    AuthDialog,
    Fab,
    Skeleton
  },
  data () {
    return {
      recordList: [],
      articleList: [],
      btnList: [{
        bgColor: "#16C2C2",
        imgUrl: "https://cloud-minapp-31286.cloud.ifanrusercontent.com/1ia4kuw27QLDWlZy.png",
        imgHeight: 64,
        imgWidth: 64,
        text: "文章",
        fontSize: 34,
        color: "#fff"
      }, {
        bgColor: "#16C2C2",
        imgUrl: "https://cloud-minapp-31286.cloud.ifanrusercontent.com/1ia4kupW0Q8csvyx.png",
        imgHeight: 64,
        imgWidth: 64,
        text: "打卡",
        fontSize: 34,
        color: "#fff"
      }
      ],
      isShow: false,
      club: {},
      memberCount: 0,
      focusOnCount: 0,
      loading: true
    }
  },
  computed: {
    ...mapGetters('user', ['hasBindClub', 'userInfo'])
  },
  async onLoad (option) {
    let self = this
    this.loading = true
    option = option
    if (option.clubId) {
      this.getClub(option.clubId)
      this.getMemberCount(option.clubId)
    } else {
      if (this.$store.getters['user/isAuth'] && this.$store.getters['user/hasBindClub']) {
        this.getOwnClub()
        this.getfocusOnCount()
      } else {
        uni.showModal({
          title: '提示',
          content: '还未绑定社团, 点击前往绑定',
          success (res) {
            if (res.confirm) {
              qq.reLaunch({ url: `/pages/user/login` })
            } else {
              qq.navigateBack()
              self.loading = false
            }
          }
        })
      }
    }
  },
  methods: {
    // 分享
    onShareAppMessage: function (e) {
      var self = this;
      return {
        title: 'RUC社团说---快来找找你的社团啦',
        imageUrl:
          'https://cloud-minapp-31286.cloud.ifanrusercontent.com/1iayhHZQUc1pbeTN.png',
        path: '/pages/finder/index'
      }
    },
    pageOnLoad: function () {
      this.onLoad(option)
    },
    // 认证回调
    authCallback (auth) {
      // TODO onload
      const { isAuth, hasBindClub } = auth
      if (hasBindClub) {
        this.pageOnLoad()
      } else {
        this.isShow = false
        uni.showModal({
          title: '提示',
          content: '查询不到你的相关社团, 如审核通过会通知提醒',
          showCancel: false,
          success (res) {
            if (res.confirm) {
              qq.reLaunch({ url: `/pages/finder/index` })
            }
          }
        })
      }
    },
    // 前往成员页
    toMemberList () {
      uni.navigateTo({
        url: `/pages/club/members?clubId=${this.club.id}`
      })
    },
    // 获取自身社团
    async getOwnClub () {
      let Member = new qq.BaaS.TableObject('club_member')
      let query = new qq.BaaS.Query()
      let User = new qq.BaaS.User()
      Member.setQuery(query.compare('user', '=', User.getWithoutData(this.userInfo.userId))).expand(['club']).find().then(res => {
        if (res.statusCode === 200 && res.data.objects.length !== 0) {
          this.club = res.data.objects[0]['club']
          this.getMemberCount(this.club.id)
          this.getArticleList(this.club.id)
          this.getRecordList(this.club.id)
        }
        this.loading = false
      }, err => { console.log('err', err) })
    },
    // 获取社团信息
    getClub (id) {
      let Club = new qq.BaaS.TableObject('club')
      Club.get(id).then(res => {
        if (res.statusCode === 200) {
          this.club = res.data
          this.getArticleList(this.club.id)
          this.getRecordList(this.club.id)
        }
        this.loading = false
      }, err => { console.log('err', err) })
    },
    // 获取打卡信息
    getRecordList (id) {
      let Record = new qq.BaaS.TableObject('record')
      let Club = new qq.BaaS.TableObject('club')
      let club = Club.getWithoutData(id)

      const query = new qq.BaaS.Query()
      query.compare('status', '=', 0)
      query.compare('club', '=', club)
      Record.setQuery(query).select('-recordList').find().then(res => {
        if (res.statusCode === 200) {
          let recordList = res.data.objects
          this.recordList = recordList.map(item => {
            item['time'] = parseTime(item.created_at, '{y}-{m}-{d}')
            return item
          })
        }
      }, err => {
        console.log('getHotRecordLiset', err)
      })
    },
    // 获取文章信息
    getArticleList (id) {
      let Article = new qq.BaaS.TableObject('article')
      let Club = new qq.BaaS.TableObject('club')
      let club = Club.getWithoutData(id)
      const query = new qq.BaaS.Query()
      query.compare('status', '=', 0)
      query.compare('club', '=', club)
      Article.setQuery(query).find().then(res => {
        if (res.statusCode === 200) {
          let articleList = res.data.objects
          this.articleList = articleList.map(item => {
            item['time'] = parseTime(item.created_at, '{y}-{m}-{d}')
            return item
          })
        }
      }, err => {
        console.log('getHotRecordLiset', err)
      })
    },
    getMemberCount (id) {
      let Member = new qq.BaaS.TableObject('club_member')
      let query = new qq.BaaS.Query()
      let Club = new qq.BaaS.TableObject('club')

      let club = Club.getWithoutData(id)
      Member.setQuery(query.compare('club', '=', club)).count().then(res => {
        this.memberCount = res
        this.getfocusOnCount()
      })
    },
    // 获取关注度
    getfocusOnCount () {
      this.focusOnCount = this.articleList.length * 42 + this.recordList * 14 + this.memberCount * 32
    },
    setFocusRulesShow () {
      uni.showModal({
        title: '关注度规则',
        content: '关注度由社团发布文章, 社团打卡活动, 参与人数以及社团关注人数等多个维度决定',
        showCancel: false,
        confirmText: '明白',
        success: () => { },
        fail: () => { }
      })
    },
    // fab悬浮点击
    onClick (e) {
      let index = e.index
      switch (index) {
        case -1:
          break;
        case 0:
          uni.navigateTo({
            url: `/pages/article/create?clubId=${this.club.id}`
          })
          break;
        case 1:
          uni.navigateTo({
            url: `/pages/record/create?clubId=${this.club.id}`
          })
          break;
        default:
          break;
      }
    },
  }
}
